import axios from "axios";
import {Dispatch} from "redux";
import {changeStatusLoadingAC, setErrorAC} from "../../app/appReducer";

export const handleServerNetworkError = (err: unknown, dispatch: Dispatch):void => {
    let errorMessage = "Some error occurred";
    if (axios.isAxiosError(err)) { //    axios 
        // err.response?.data?.message -      todolistId
        // err?.message -      offline 
        errorMessage = err.response?.data?.message || err?.message || errorMessage;

    } else if (err instanceof Error) {  //     
        errorMessage = `Native error: ${err.message}`;
    } else {
        errorMessage = JSON.stringify(err);// -  
    }
    dispatch(setErrorAC( errorMessage));
    dispatch(changeStatusLoadingAC("failed" ));
};